import React from 'react';


const FooterLinks=({col,maintitle,a,b,c})=>{
    return(
        <>
            <div className={col}>
                <div className="footer-link">
                    <h6 className="footer-title">{maintitle}</h6>
                    <ul>
                        <li><a href="#">{a}</a></li>
                        <li><a href="#">{b}</a></li>
                        <li><a href="#">{c}</a></li>
                    </ul>
                </div> 
            </div>
        </>               
    );
}

export default FooterLinks;
